.navContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 60px;
  padding: 18px 20px;
  background-color: var(--secondaryBgColor);
}



.buttonBurger {
  width: 24px;
  height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--secondaryBgColor);
}

@media screen and (min-width: 1024px) {
  
  .navContainer{
    justify-content: flex-end;
  }

  .buttonBurger {
    display: none;
  }
}

.iconBurger {
  border-radius: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  stroke: var(--mainTextColor);
}

.navMenu {
  display: flex;
  align-items: center;
  gap: 14px;
}
