.sideBar {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  top: 0;
  left: 0;
  width: 225px;
  height: 100vh;
  background-color: var(--sidebarBgColor);
  /* transform: translateX(-100%); */
  transition: 500ms transform;
}

@media screen and (min-width: 768px) {
  .sideBar {
    width: 260px;
  }
}

@media screen and (min-width: 1024px) {
  .sideBar {
    width: 260px;
    position: static;
    transform: none;
  }
}

@media screen and (min-width: 1440px) {
  .sideBar {
    position: static;
    width: 310px;
    transform: none;
  }
}

/* має працювати як буде кнопка відкриття */

.sideBarClose {
  display: none;
}

@media screen and (min-width: 1024px) {
  .sideBarClose {
    display: unset;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    top: 0;
    left: 0;
    width: 260px;
    height: 100vh;
    background-color: var(--sidebarBgColor);
  }
}



